import { MySqlConnectionOptions } from './driver/my-sql/my-sql-connection.options';
import { PostgresConnectionOptions } from './driver/postgres/postgres-connection.options';
import { SqlServerConnectionOptions } from './driver/sql-server/sql-server-connection.options';

export type DatasourceOptions =
  | MySqlConnectionOptions
  | PostgresConnectionOptions
  | SqlServerConnectionOptions;

/**
 * @Deprecated Set validation in api
 */
export function isDatasourceOptions(
  obj: DatasourceOptions,
): obj is DatasourceOptions {
  return (
    obj !== undefined &&
    obj.type !== undefined &&
    typeof obj.type === 'string' &&
    (obj.type === 'postgres' || obj.type === 'mysql' || obj.type === 'mssql')
  );
}
